const ADD_POST = 'ADD-POST';
const UPDATE_TEXT_POST = 'UPDATE-TEXT-POST';
const UPDATE_LIKES_COUNTER = 'UPDATE-LIKES-COUNTER';

const initialStore = {
  posts: [
    { id: 0, text: 'Go to fight', likeCounter: 10, date: 'July 1, 2018 07:22' },
    { id: 1, text: 'Go to dance', likeCounter: 213, date: 'Aug 22, 2020 22:21' },
    { id: 2, text: 'Go to drink', likeCounter: 146, date: 'Aug 22, 2020 06:12' },
  ],
  newPostText: ''
};

const renderLikesCounter = (id, value) => {
  initialStore.posts.forEach((el) => {
    if (el.id === id) {
      return el.likeCounter = value;
    }
  })
};

const profileReducer = (state = initialStore, action) => {
  let stateCopy = {
    ...state,
    posts: [...state.posts]
  };
  switch (action.type) {
    case UPDATE_TEXT_POST:
      stateCopy.newPostText = action.text;
      return stateCopy;
    case ADD_POST:
      let idCounerPost = stateCopy.posts.length + 1;
      const newPost = {
        id: idCounerPost,
        text: stateCopy.newPostText,
        likeCounter: 0,
        date: new Date()
      };
      stateCopy.posts.unshift(newPost);
      stateCopy.newPostText = '';
      return stateCopy;
    case UPDATE_LIKES_COUNTER:
      renderLikesCounter(action.id, action.value);
      return stateCopy;
    default:
      return stateCopy;
  }
};

export const actionSendPost = () => {
  return {
    type: ADD_POST
  }
};

export const actionOnChangePostText = (text) => {
  return {
    type: UPDATE_TEXT_POST,
    text: text
  }
};

export const actionLikePost = (id, value) => {
  return {
    type: UPDATE_LIKES_COUNTER,
    id: id,
    value: value
  }
};

export default profileReducer;
