import React from 'react';
import './OpenDialog.css';

class OpenDialog extends React.Component {
  constructor(props) {
    super(props);
    this.mesRef = React.createRef(null);
  };

  componentDidMount() {
    this.mesRef.current.scrollIntoView();
  };

  componentDidUpdate() {
    this.mesRef.current.scrollIntoView({behavior: 'smooth'});
  };

  render() {
    return (
      <div className='dialogs__is-open'>
        <ul className='dialogs__message-list'>
          {this.props.renderMessages}
          <div ref={this.mesRef}></div>
        </ul>
        <form className='dialogs__send-message' onSubmit={this.props.sendMessage}>
          <textarea
            className='dialogs__input'
            placeholder='enter a message...'
            onChange={this.props.onChangeMessageText}
            value={this.props.messagesPage.newMessageText}
            autoFocus
            required />
          <button className='dialogs__send-button' >{'>'}</button>
        </form>
      </div>
    )
  }
};

export default OpenDialog;
